h1, h2, p, ul, li {
  font-family: sans-serif;
}
ul.mainheader li {
  display: inline;
  list-style-type: circle;
  margin: 0;
}
ul.mainheader {
  background-color: #000000;
  position: absolute;
  left: 0;
  top:0px;
  width: 100%;
  height: 75px;
}

div.image {
  height: 0px;
  width: 0px;
}

ul.mainheader li a {
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 30px 5px;
  display: inline-block;
  float: right;
  margin-right: 10%;

}

ul.mainheader li a:hover {
  color: red;
}

ul.mainheader img {
  float: left;
  height: 50px;
  max-width:100%;
  margin-left: -10px;
  margin-top: 10px;
}

.mainContent {
  background-color: #FFF;
  padding: 20px;
}
.mainContent h2 {
  padding: 0;
  margin: 0;
}
.mainContent li {
  margin-bottom: 10px;
}

.homeCarousel{
  position: fixed;
  display: flex;
  left: 0;
  top: 75px;
  bottom: 200px;
  pointer-events: none;

}

.homeCarousel img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.appFooter{
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  height: 100px;
  bottom: 200px;

}

.appFooter img{
  margin: 10px;
}

.mainFooter{
  position: absolute;
  width: 100%;
  height: 100px;
  padding: 25px;
  left:50%;
  bottom: 0px;
  transform: translate(-50%,-100%);
  color: white;
  text-align: center;
  background-color: #000000;
}

.mainFooter span{
  color: white;
  padding: 10px;
}

@media only screen and (max-width: 650px) {
  .appFooter img{
  width: 130px;
  height: 50px;
  }
}

@media only screen and (max-width: 550px) {
  .mainheader img {
    position: absolute;
    left: 15px;
    top: 5px;
  }
}

@media only screen and (max-width: 450px) {
  .appFooter img {
    width: 80px;
    height: 30px;
  }
}


@media only screen and (max-height: 380px) {
  .homeCarousel{
    bottom: 100px;
  }
  .mainFooter {
    bottom: -50px;
  }
  .appFooter{
    bottom: 150px;
  }
}

@media only screen and (max-width: 350px) {
  .mainheader img{
  position: absolute;
    width: 130px;
    left: 15px;
    top: 5px;
  }
}