.form-control {
  width: 50%;
  border: 1px solid #ccc;
  padding: 10px;
}

.control-error {
  border: 1px solid red;
}

.form{
  margin: 0 auto;
  margin-top: 100px;
  max-width: 800px;

}

.form h1{
  color: #282c34;
  align-self: flex-start;
}

.form a{
  color: #282c34;
}

.row {
  display: flex;
}

.first {
  width: 300px;
  margin-right: 50px;
  margin-top: 5px;
}

.second {
  width: 300px;
  margin-top: 5px;
}

.contactFooter{
  position: absolute;
  width: 80%;
  height: 100px;
  left:50%;
  transform: translate(-50%,50%);
  color: white;
  text-align: center;
}

.contactFooter span{
  color: white;
  padding: 10px;
}